import React from 'react';
import s from './Friends.module.css';
import { NavLink } from 'react-router-dom';

const Friends = (props) => {
        return <div className={s.friendWrapper}>
            <div>
            <img src={props.avatar} className={s.friendAvatar} />
            </div>
            <div>
            {props.name}
            </div>
    </div>
}

export default Friends;