import { profileAPI } from "../api/api";

const ADD_POST = "samurai-network/profile/ADD-POST";
const SET_USER_PROFILE = "samurai-network/profile/SET_USER_PROFILE";
const SET_STATUS = "samurai-network/profile/SET_STATUS";
const DELETE_POST = "samurai-network/profile/DELETE_POST";

let initialState = {
    posts: [
        { id: 1, message: "Hi, how are you?", likeCounts: 12 },
        { id: 2, message: "It's my first post", likeCounts: 10 },
    ],
    profile: null,
    status: ""
}

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST:
            let newPost = {
                id: 5,
                message: action.newPostText,
                likeCounts: 0
            };
            return {
                ...state,
                posts: [...state.posts, newPost],
            }
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            }
        case SET_STATUS:
            return {
                ...state,
                status: action.status
            }
        case DELETE_POST:
            return {
                ...state,
                posts: state.posts.filter(p => p.id !== action.postId)
            }

        default:
            return state;
    }
}

export const addPostActionCreator = newPostText => ({ type: ADD_POST, newPostText })

export const setUserProfile = profile => ({ type: SET_USER_PROFILE, profile })

export const setUserStatus = status => ({ type: SET_STATUS, status })

export const deletePost = postId => ({ type: DELETE_POST, postId })

export const getUserProfile = (userId) => async (dispatch) => {

    let data = await profileAPI.getProfile(userId)
    dispatch(setUserProfile(data))
}

export const getUserStatus = (userId) => async (dispatch) => {

    let data = await profileAPI.getStatus(userId)
                dispatch(setUserStatus(data))
}

export const updateStatus = (status) => async (dispatch) => {
    let data = await profileAPI.updateStatus(status)
                if (data.resultCode === 0) {
                    dispatch(setUserStatus(status))
    }
}

export default profileReducer;