import s from './Navbar.module.css';
import React from 'react';
import complectCLassNames from '../../helpers/complectCLassNames';
import { NavLink } from 'react-router-dom';
import Friends from '../Friends/Friends';


const Navbar = (props) => {
    let sideBar = props.sideBar;
    let friend = sideBar.
        map(friend => <Friends id={friend.id} name={friend.name} avatar={friend.avatar} />)

    return <nav className={s.nav}>
        <div className={s.shortcutsWrapper}>
            <div className={complectCLassNames([s.item, s.active])}>
                <NavLink to="/profile" activeClassName={s.active}>Profile</NavLink>
            </div>
            <div className={s.item}>
                <NavLink to="/dialogs" activeClassName={s.active}>Messages</NavLink>
            </div>
            <div className={s.item}>
                <NavLink to="/news" activeClassName={s.active}>News</NavLink>
            </div>
            <div className={s.item}>
                <NavLink to="/music" activeClassName={s.active}>Music</NavLink>
            </div>
            <div className={s.item}>
                <NavLink to="/settings" activeClassName={s.active}>Settings</NavLink>
            </div>
            <div className={complectCLassNames([s.item, s.findFriends])}>
                <NavLink to="/users" activeClassName={s.active}>Users</NavLink>
            </div>
        </div>
        <div className={complectCLassNames([s.item, s.sidebarFriends])}>
            <div className={s.friends}>
                FRIENDS
            </div>
            <div className={s.friendsWrapper}>{friend}</div>
        </div>
    </nav>
}

export default Navbar;